//
.header{
    position: relative;
    height: 100vh;
}
.header__slider{
    &-item{
        height: 100vh;
        background-image: url(/img/header-bg.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;
    }
}
.slider-item__info{
    position: absolute;
    right: 80px;
    bottom: 115px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    color: #fff;
   
}
.slider-item__info-subtitle{
    margin-bottom: 5px;
}
.slider-item__info-subtext{
    margin-bottom: 8px;
}
.slider-item__info-title{
    font-weight: 800;
    font-size: 80px;
    line-height: 86px;
    
    margin-bottom: 48px;  
}
.slider-item__info-text{
    font-size: 40px;

    font-weight: 800;
    line-height: 86px;
}
.slider-arrows{
    position: absolute;
    top: 40px;
    right:80px;
    cursor: pointer;
    z-index: 4;
    &--surf {
        top: -67px;
        opacity: .25;
    }
    &--travel {
        top: 135px;
        
    }
}
.slider-arrows__left{
    right: 125px;
}
.slider-dots{
    position: absolute;
    right: 50px;
    bottom: 36px;
    width: 100%;
    max-width: 920px;
    

    .slick-current {
        .dots-box {
            border-top: 4px solid #4AF6CD;
        }
    }
}
.dots-box{
    max-width: 200px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 6px;
    align-items: center;
    
    
    border-top: 4px solid #fff;

    &__number{
        font-weight: 800;
        font-size: 24px;
        line-height: 34px;
    }
    &__name{
        font-weight: 300;
        text-transform: capitalize;
    }
    
}
.header__aside{
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5 ;
    border-right: 1px solid rgba(#fff, .25);
    height: 100vh;
}
.header__title{
    font-weight: 800;
    font-size: 200px;
    line-height: 156px;
    color: #FFFFFF;
    opacity: 0.07;
    max-width: 467px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4 ;
}
.menu{
    
        padding-top: 40px;
    a{
        
        
        span{
            text-transform: capitalize;
            margin-bottom: 22px;
            padding-top: 6px;
            display: block;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 3px;

        }
    }

    
}
.header__search{
    margin-bottom: 20px;
}
.header__date{
    font-weight: 800;
    font-size: 72px;
    line-height: 64px;
    
    span{
        padding-top: 15px;
        display: block;
        font-weight: 300;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 1.4px;
        margin-bottom: 30px;
    }
}
.header__location{
    display: inline-block;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 1.05px;
    position: relative;
    padding-left: 19px;
    text-transform: capitalize;
    &::before{
        content: "";
        position: absolute;
        
        width: 11px;
        height: 13px;
        background-image: url(/img/marker.svg);
        left: 0;
        top: 0;

    }
}
.header__map{
    position: absolute;
    right: 0;
    top: 0;
    z-index: 3;
}
.header__arrows{
    display: inline-block;
    position: absolute;
    z-index: 3;
    bottom: 40px;
    left: 210px;
}
