.surf{
    padding: 160px 0 120px;
    background-image: url("/img/surf-bg.png");
    background-position: right 200px;
    background-repeat: no-repeat;
    background-size: auto;
}

.surf-slider{
    margin-top: 110px;

    .slick-slide:nth-child(4n + 1) {
        .surf-box{
            margin-top: 32px;
            
        }
    }
    .slick-slide:nth-child(4n + 3) {
        .surf-box{
            margin-top: 92px;
            
        }
    }
    .slick-current{
        .surf-box__inner-btn{
            display: block;
        } 
        .surf-box__inner{
            background-color: rgba(#1C2121, .3);
        }

    }
}

.surf-box{
    
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    

    &__inner{
        min-height: 750px;
        background-color: rgba(#1C2121, .85);
        position: relative;
        top: 0;
        bottom: 0;
        transition: all .5s;
        &:hover{
            background-color: rgba(#1C2121, .3);
            .surf-box__inner-btn{
                display: block;
            }
        }
    }
    
}
.surf-box__inner-title{
    font-weight: 800;
    font-size: 40px;
    line-height: 36px;
    padding:72px 0 0 40px;
    width: 140px;
}
.surf-box__inner-location{
    position: absolute;
    width: 0;
    
    height: 0;
    left: 40px;
    bottom: 80px;
    white-space: nowrap;
    
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 8px;
    transform: rotate(-90deg);

    span {
        font-weight: 100;
    }
}
.surf-box__inner-btn{
    position: absolute;
    text-align: center;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
}
.slider-map {
    max-width: 1200px;
    min-height: 635px;
    background-image: url(/img/world-map-dots.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    margin: 0 auto;
    .slick-list{
        min-height: 635px;
    }
    .slick-current{
        .slider-dots__content{
            display: block;
        }
    }
}
.slider-dots__circle{
    width: 23px;
    height: 23px;
    background-color: #4AF6CD;
    border-radius: 50%;
}
.slider-dots__content-item{
    text-align: center;
    img{
        margin-bottom: 7px;
    }
}
.slider-dots__content-number{
    font-weight: 800;
    font-size: 16px;
    line-height: 23px;
}

.slider-dots__content-name{
    font-weight: 300;
    font-size: 10px;
    line-height: 12px;
}

.slider-dots__content{
    background-color: rgba(#0F1010, .7);
    border-radius: 20px;
    position: absolute;
    bottom: 35px;
    transform: translateX(-45%);
    display: none;
    width: 285px;
    padding: 32px 30px 0 32px;
    z-index: 3;

    &::before{
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        bottom: -22px;
        left: 44.9%;
        border-bottom: 20px solid #4AF6CD;
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
    }
}
.slider-dots__content-row{
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
}
.slider-dots__content-link{
    padding-top: 4px;
}
.slider-dots__content-title{
    font-weight: 800;
    font-size: 18px;
    line-height: 26px;
    padding-bottom: 8px;
    margin-bottom: 8px;
    position: relative;

    &::before{
        content: "";
        position: absolute;
        background-color: #4AF6CD;
        width: 80px;
        height: 1px;
        bottom: 0;
        left: 0;
    }
}
.slider-dots__content-subtitle{
    text-transform: capitalize;
    font-size: 16px;
    line-height: 19px;
}
.slider__dots{
    position: relative;
    z-index: 2;
}
.slider-dots--1{
    top: 335px;
    left: 0;
    .slider-dots__content{
       transform: translateX(0);
        &::before{
            left: 0;
        }   
      
    }
}
.slider-dots--2{
    top: 275px;
    left: -1.5%;

    .slider-dots__circle{
    position: relative;
        &::before{
            content: "";
            position: absolute;
            background-image: url(/img/mini-marker.svg);
            width: 15px;
            height: 17px;
            background-size: cover;
            right: -15px;
            top: -15px;
        }
    }
    
}
.slider-dots--3{
    top: 320px;
    left: 3%;
}
.slider-dots--4{
    top: 500px;
    left: -3%;
}
.slider-dots--5{
    top: 215px;
    left: -6%;
}
.slider-dots--6{
    top: 555px;
    left: -9.5%;
}
.slider-dots--7{
    top: 385px;
    left: -8%;
}
.slider-dots--8{
    top: 500px;
    left: 3%;
    .slider-dots__content{
        transform: translateX(-92%);
         &::before{
             left: auto;
             right: 0;
         }   
       
     }
}
.holder{
    background-image: url(/img/travel-bg.png);
    
    background-repeat: no-repeat;
    background-position: bottom right;
    
    &--sleep{
        background-image: url(/img/sleep-bg.png);
        background-position: right 1000px;
        z-index: 5;
    }
}

.holder-slider{
    &__head{
        background-repeat: no-repeat;
        background-position: top left;
        min-height: 780px;

    
    }
    .descr{
        padding-bottom: 130px;
        padding-left: 135px;
        
    &--sleep{
        img{
            padding-left: 0;
            margin-right: -25px;


        
        }

        
    }
   
    
    }
    &__descr {
        margin-top: -230px;
        min-width: 320px;
       
        background-repeat: no-repeat;
        background-position: top right;
        padding-top: 160px;
    }
    .subtitle{
        padding-top: 130px;
        
    }
    &-btn{
       
        text-align: center;
    }
    
}
.holder-slider__info{
    
    display: flex;
    justify-content: space-around;
}
.holder-slider__info-item{
    min-height: 200px;
    margin-bottom: 66px;
    position: relative;
}
.holder-slider__info-subtitle{
    position: absolute;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 8px;
    opacity: 0.5;
    transform: rotate(-90deg);
    top: 96px;
    text-align: right;
    
    left: -97px;
    width: 200px;
    height: 0;
    white-space: nowrap;
    
}
.holder-slider__info-title{
    position: relative;
    font-weight: 800;
    font-size: 40px;
    line-height: 36px;
    padding-left: 40px;
    
   

    
    .holder-pricing__span {
        display: block;
        text-align: right;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        text-align: right;
        letter-spacing: 5.25px;
        opacity: 0.75;
        
    }
}
.rating{
    margin-left: -10px;
    display: flex;
}
.rating-star{
    margin-right: -20px;
}

.shop{
    background-image: url(/img/shop-slider-bg.png);
    background-repeat: no-repeat;
    background-position: right center;
    .slider-arrows{
        top: -50px;
    }
}
.shop__slider{
    padding-left: 132px;
}
.shop__slider-inner{
    display: flex;
    width: 100%;
}
.shop__slider-info{
    max-width: 480px;
    margin-right: 40px;
    padding-top: 166px;
    
}
.shop__slider-price{
    font-weight: 800;
    font-size: 32px;
    line-height: 24px;

    sup{
        top: -10px;
        font-size: 18px;
    }
}
.shop-slider-btn{
    margin-top: 64px;
}
.shop__slider-content{
    padding-left: 270px;
    display: flex;
    align-items: flex-end;
}
.shop__extras__inner{
    display: flex;
    
}
.shop__extras{
    padding-left: 150px;
}
.shop__extras-title{
    margin-bottom: 25px;
    font-weight: 800;
    font-size: 28px;
    line-height: 40px;
}
.shop__extras-box{
    text-align: center;
    margin-right: 57px;

    
}
.shop__extras-name{
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 8px;
    opacity: 0.5;
    margin-bottom: 16px;
}
.shop__extras-pric{
    font-weight: 800;
    font-size: 18px;
    line-height: 40px;
    
}
.shop__extras-img{
    min-height: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 35px;
}
.surfboard{
    position: relative;
}
.surfboard-box{
    position: absolute;
    
}
.surfboard__position--1{
    right: 40px;
    top: 212px;
}
.surfboard__position--2{
    left: -6px;
    top: 292px;
}
.surfboard__position--3{
    left: 52px;
    bottom: 69px;
    
    .surfboard-box__content{
        padding-left: 0;
        padding-right: 104px;
        top: -15px;
        left: -395px;
        &::before{
            content: "";
            position: absolute;
            width: 80px;
            height: 1px;
            background-color: #fff;
            right: 0;
            left: auto;
        }
    }
}
.surfboard-box__circle{
    width: 20px;
    height: 20px;
    position: relative;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;


    &.active {
        .circle-row{
             transform: translate(-50%, -50%) rotate(0deg);
        }
        

    }
}
.circle-row{
    background-color: #4AF6CD;
    position: absolute;
    width: 12px;
    height: 2px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(0deg);
    & + .circle-row {
       transform: translate(-50%, -50%) rotate(90deg);
    }
}
.surfboard-box__circle.active + .surfboard-box__content{
    display: block;
}
.surfboard-box__content{
    display: none;
    position: absolute;
    width: 359px;
    font-weight: 800;
    font-size: 24px;
    line-height: 24px;
    top: -15px;
    left: 40px;
    padding-left: 104px;

    &::before{
        content: "";
        position: absolute;
        width: 80px;
        height: 1px;
        background-color: #fff;
        left: 0;
        top: 50%;
    }
}
.footer{
    text-align: center;
    padding: 285px 0 34px 0;

    .btn{

        span{
            padding: 17px 17px 15px 17px;
            &::after{
                display: none;
            }
        }
    }
}
.footer__copy {
    padding-top: 27px;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 1.05px;
    opacity: 0.5;
}